import React from 'react';

const description = <p> Un atelier de médiation sous forme de discussion, créé en collaboration avec Tel-jeunes est offert suite à la pièce afin de poursuivre les réflexions que la pièce pourrait susciter chez les élèves et par le fait même encadrer les élèves qui auraient des questions suite à ce qu’ils auront vu. Lors de l’atelier, nous aborderons les thèmes de la pièce et nous présenterons les enjeux de chaque personnage tout en offrant certaines pistes de solution par rapport à ce qu’ils auront exprimé. Les comédiens parleront également de leur vécu face à leur propre passage à l’âge adulte et pour certains de leur combat avec les troubles anxieux. Nous souhaitons démystifier avec  les étudiants les différentes formes que peuvent prendre les troubles anxieux ou l’anxiété en général et ainsi mettre des mots sur des maux qu’on ne comprend pas toujours. L’expérience <em>Le confessionnal</em> est en soi un outil pour amorcer une discussion sur la santé mentale et dédramatiser d’une certaine façon le passage à l’âge adulte. Par le médium du théâtre, les étudiants pourront mieux aller à la rencontre de leurs émotions et de ce qu’ils appréhendent face à leur avenir. C’est ce que nous voulons offrir aux étudiants : une ouverture sur ce qu’ils ressentent, sans jugement; un début de cheminement pour mettre un terme aux tabous vis-à-vis la santé mentale.</p>;

export default description;