import React from 'react';
let subtitleStyle={
    fontWeight: "bold",
    fontSize: "25px",
    color: "black"
  }
const confessionnal = <p style={subtitleStyle}>Le Théâtre Point Virgule en collaboration avec Tel-jeunes présente <br/><em>Le confessionnal</em>, une pièce de théâtre pour adolescents qui traite de l’anxiété provoquée par le passage à l’âge adulte. La pièce de théâtre sera suivie d’un atelier de médiation d’intervention créé en collaboration avec l’équipe de Tel-jeunes. <div>Depuis 2021, l’activité est également offerte en ligne.</div></p>;

export default confessionnal;